.give-ach {
  box-sizing: border-box;
  padding: 15px;
  background: $color-lightblue img-url('waves.svg');
  outline: 1px solid $color-text;
  outline-offset: -6px; }

.give-ach__label {
  display: block;
  margin-bottom: 6px;
  text-align: center;
  @include f(t, 15);
  line-height: 1;
  color: $color-text;
  text-transform: uppercase; }

.give-ach__row {
  margin: 21px 0 25px;
  // +clr
  display: table;
  width: 100%;
 }  // table-layout: fixed

.give-ach__sum {
  display: table-cell;
  vertical-align: middle;
  background: #FFF;
  color: $color-text;
  font-size: 28px;
  // box-sizing: border-box
  // min-width: 112px
  width: 112px;
  height: 34px;
  line-height: 34px;
  padding: 0 10px;
  text-align: center;
  // float: left
 }  // margin-left: -112px

.give-ach__lines {
  display: table-cell;
  vertical-align: middle;
  // float: left
  // width: 100%
  margin-top: 4px;
  box-sizing: border-box;
  // padding-right: 134px
  padding-right: 24px;
  &:before {
    content: "";
    display: block;
    height: 26px;
    border: 1px solid $color-text;
    border-width: 1px 0;
    box-sizing: border-box; } }
