.give-help-container {
  display: inline-block;
  position: relative;
  z-index: 0;
  .give-icon {
    z-index: 1; }
  &:hover,
  &.is-hover {
    .give-icon {
      display: block;
      color: $color-blue;
      opacity: 1; }
    .give-help-popup {
      opacity: 1;
      visibility: visible;
      transform: scale(1); } } }

.give-help-popup {
  opacity: 0;
  visibility: hidden;
  transform: scale(0.8);
  transform-origin: bottom right;
  transition: all 0.3s ease;
  position: absolute;
  z-index: 0;
  bottom: 100%;
  margin-bottom: 28px;
  right: 8px;
  background: #FFF;
  width: 256px;
  padding: 15px;
  box-sizing: border-box;
  border: 1px solid $color-text;
  border-radius: 3px 3px 0 3px;
  text-align: center;
  font-size: 15px;
  line-height: 1.4;
  &:before {
    content: "";
    position: absolute;
    top: 100%;
    right: -1px;
    @include arr(44, 24, $color-text, tr); }
  &:after {
    content: "";
    position: absolute;
    top: 100%;
    right: 0;
    margin-top: -1px;
    @include arr(42, 23, #FFF, tr); }
  a, b, strong {
    @include f(b); }
  a {
    color: $color-blue;
    text-decoration: underline;
    &:hover {
      text-decoration: none; } }
  p {
    margin: 1em;
    &:first-child {
      margin-top: 0; }
    &:last-child {
      margin-bottom: 0; } }
  .give-icon {
    display: block;
    text-align: center;
    font-size: 24px;
    opacity: 1;
    color: $color-blue; } }

