button.give-icon {
  background: none; }

.give-title {
  @include f(b, 30);
  line-height: 1;
  margin: 0 0 20px;
  &._size-sm {
    font-size: 24px;
    margin-bottom: 19px; }
  &._size-xsm {
    font-size: 18px; }
  &._size-lg {
    font-size: 45px; } }

.give-header {
  margin: 0; }

.give-header-links {
  padding: 0 20px;
  @include f(t, 14);
  line-height: 1;
  text-align: right;
  a {
    text-decoration: none;
    color: inherit;
    &:hover {
      text-decoration: underline; } } }

.give-tagline {
  margin: 21px 0 0;
  padding: 0 20px;
  @include f(t, 18px);
  line-height: 1.278;
  text-align: center; }

.give-banner {
  position: relative;
  margin: 14px 0 21px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  &:before {
    content: "";
    display: block;
    width: 100%;
    padding-bottom: percentage(115/688); }
  > img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    min-width: 100%;
    min-height: 100%;
    max-width: 100%; } }


.give-widget {
  padding-top: 13px;
  width: 100%;
  max-width: $widget-max-width;
  margin: auto; }

.give-widget-body {
  padding: 20px;
  position: relative;
  z-index: 0;
  .give-thank {
    position: absolute;
    z-index: 10;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; } }

.give-widget-body__inner {
  border: 1px solid $color-text;
  border-radius: 3px;
  padding-bottom: 24px;
  position: relative; }

.give-widget-body__main {
  position: relative; }

.give-widget-body__bottom {
  padding: 0 0 24px; }


.give-widget-main-container {
  overflow: hidden;
  @include clr; }

.give-calculator {
  box-sizing: border-box;
  width: 100%; }

.give-footer {
  padding: 0 20px 24px; }

.give-footer__inner {
  display: table;
  table-layout: fixed;
  border-spacing: 0;
  width: 100%;
  @include f(t, 10);
  color: #546475;
  line-height: 1; }

.give-footer__cell {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
  &:first-child {
    text-align: left;
    width: 20%; }
  &:last-child {
    text-align: right;
    width: 20%; }
  .give-icon {
    font-size: 18px;
    opacity: 0.2;
    color: inherit;
    text-decoration: none;
    @include trans();
    position: relative;
    &:hover {
     color: $color-blue;
     opacity: 1; } } }

.give-footer__copyright {
  > span, img, a {
    display: inline-block;
    vertical-align: middle;
    opacity: 0.5; }
  > span {
    margin-right: 4px; }
  > a {
    @include trans(opacity);
    &:hover {
      opacity: 1; } } }


.give-tabs {
  display: table;
  table-layout: fixed;
  border-collapse: separate;
  border-spacing: 0;
  width: 100%;
  list-style-type: none;
  padding: 0;
  margin: 0;
  position: relative;
  box-sizing: border-box; }

.give-tabs__list {
  display: table-row;
  margin: 0;
  padding: 0;
  list-style-type: none; }

.give-tabs__item {
  display: table-cell;
  vertical-align: middle;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  border: 1px solid $color-text;
  border-width: 0 1px 0 0;
  box-shadow: inset 0 -1px 0 0 $color-text;
  &:last-child {
    border-right: none; }
  &.is-active {
    box-shadow: none;
    .give-tabs__button {
      opacity: 1; } } }

.give-tabs__button {
  display: block;
  width: 100%;
  @include f(b, 14);
  line-height: 1;
  text-transform: uppercase;
  height: 46px;
  box-sizing: border-box;
  opacity: 0.5;
  color: $color-text;
  @include trans;
  user-select: none;
  &:hover {
    opacity: 1; } }

.give-presets {
  padding: 24px 24px 8px;
  position: relative;
  .give-dots {
    display: none; } }

.give-presets__slides {
  .slick-dots {
    padding: 0;
    margin: 0;
    position: absolute;
    top: -16px;
    left: 0;
    right: 0;
    font-size: 0;
    text-align: center;
    li {
      display: inline-block;
      vertical-align: middle;
      font-size: 0;
      width: 8px;
      height: 8px;
      box-sizing: border-box;
      border-radius: 50%;
      border: 1px solid $color-border-grey;
      margin-right: 6px;
      @include trans(background-color);
      &:last-child {
        margin-right: 0; }
      &.slick-active {
        background: $color-border-grey; } }
    button {
      font-size: 0; } } }

.give-presets__slide {
  position: relative;
  display: none;
  &:focus {
    outline: none; }
  &.is-active,
  &.slick-slide {
    display: block; } }

.give-presets__list {
  list-style-type: none;
  text-align: center;
  overflow: hidden;
  font-size: 0;
  margin: 0;
  padding: 0; }

.give-presets__item {
  display: inline-block;
  vertical-align: top;
  width: 25%;
  box-sizing: content-box;
  border: solid $color-text;
  border-width: 0 1px 1px 0;
  margin: 0 -1px -1px 0;
  &:hover {
    border-color: $color-blue; }
  &:last-child {
    width: 50%;
    width: calc(50% - 1px);
    border-left-width: 1px;
    .give-presets__button {
      font-size: 30px; } } }

.give-presets__button {
  display: block;
  width: 100%;
  @include f(t, 34);
  color: $color-text;
  line-height: 1;
  height: 70px;
  box-sizing: border-box;
  padding: 5px;
  user-select: none;
  @include trans();
  > sup {
    font-size: 18px; }
  &:hover,
  &:focus,
  &.is-active {
    background: $color-blue;
    color: #FFF; } }

.give-prev-button,
.give-next-button {
  position: absolute;
  top: 0;
  width: 24px;
  height: 100%;
  font-size: 13px;
  color: inherit;
  @include trans(background-color);
  &:before {
    position: relative;
    top: 8px; }
  &:active {
    color: inherit; }
  &:hover {
    background: #FFF; } }

.give-prev-button {
  left: 0; }

.give-next-button {
  right: 0; }

.give-check {
  display: inline-block;
  position: relative;
  @include f(t, 15);
  color: inherit;
  user-select: none;
  &._important {
    .give-check__text {
      &:before {
        border-width: 2px; } } } }

.give-check__input {
  position: absolute;
  top: 50%;
  left: 0;
  margin-top: -0.5em;
  opacity: 0; }

.give-check__text {
  display: inline-block;
  position: relative;
  padding-left: 32px;
  line-height: 24px;
  &:before {
    content: "";
    position: absolute;
    z-index: 0;
    top: 50%;
    left: 0;
    width: 24px;
    height: 24px;
    box-sizing: border-box;
    border: 1px solid;
    margin-top: -12px;
    background: #FFF;
    cursor: pointer; }
  &:after {
    opacity: 0;
    visibility: hidden;
    content: "";
    position: absolute;
    z-index: 1;
    top: 50%;
    left: 6px;
    width: 12px;
    height: 12px;
    margin-top: -6px;
    background: $color-blue;
    cursor: pointer;
    @include trans(); } }

.give-check__input:checked ~ .give-check__text,
.give-check__input[checked] ~ .give-check__text {
  &:after {
    opacity: 1;
    visibility: visible; } }

.give-select {
  display: inline-block;
  padding: 11px 39px 11px 11px;
  height: 40px;
  box-sizing: border-box;
  border: 1px solid $color-text;
  border-radius: 3px;
  position: relative;
  &:after {
    @extend %give-icon;
    content: $give-icon-chevron-down;
    position: absolute;
    z-index: 0;
    top: 50%;
    right: 13px;
    // font-size: 9px
    font-size: 12px;
    color: $color-text;
    margin-top: -0.5em; }

  &._width-fluid {
    display: block; }

  &._expires {
    display: block;
    background: #FFF;
    padding-right: 24px;
    &:after {
      right: 10px; }
    .give-select__value {
      @include f(t, 15);
      color: $color-border-grey; } }

  &._grey-border {
    border-color: $color-border-grey; } }

.give-select__value {
  display: block;
  max-width: 250px;
  position: relative;
  z-index: 0;
  @include text-overflow;
  @include f(b, 15);
  line-height: 16px;
  color: $color-blue; }

.give-select__hidden {
  opacity: 0;
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  appearance: none; }

.give-select__list {
  position: absolute;
  position: absolute;
  top: 100%;
  left: 0;
  min-width: 200px; }

.give-check-group {
  margin: 24px 0;
  font-size: 15px;
  padding-left: percentage(70/646); }

.give-check-group__row {
  margin: 16px 0;
  &:first-child {
    margin-top: 0; }
  &:last-child {
    margin-bottom: 0; }
  .give-select {
    top: 1px;
    margin: 0 0 0 4px; }
  > * {
    vertical-align: middle; } }

.give-check-group__cell {
  display: inline-block;
  vertical-align: middle;
  margin-right: 5px;
  > * {
    vertical-align: middle; } }

.give-submit-button {
  position: relative;
  z-index: 0;
  display: block;
  width: 100%;
  margin: auto;
  max-width: 300px;
  text-align: center;
  height: 60px;
  background: $color-green;
  box-shadow: inset 0px -4px 0px 0px rgba(0,0,0,0.10);
  border-radius: 3px;
  @include f(b, 20);
  line-height: 1;
  text-transform: uppercase;
  color: #FFF;
  overflow: hidden;
  @include trans;
  &:hover {
    background: lighten($color-green, 7%); }
  &:active,
  &:disabled,
  &[disabled] {
    box-shadow: none;
    background: darken($color-green, 10%); }
  &:disabled,
  &[disabled] {
    cursor: default; } }

.give-keyboard {
  max-width: 300px;
  // margin: 20px auto 8px
  margin: 0 auto 8px; }

.give-keyboard__table {
  display: table;
  width: 100%;
  border-spacing: 0;
  table-layout: fixed; }

.give-keyboard__row {
  display: table-row;
  &:first-child {
    .give-keyboard__cell {
      border-top: none; } }
  &:last-child {
    .give-keyboard__cell {
      border-bottom: none; } } }

.give-keyboard__cell {
  display: table-cell;
  text-align: center;
  vertical-align: middle;
  border-style: solid;
  border-color: $color-text;
  border-width: 0 1px 1px 0;
  padding: 0;
  &:first-child {
    border-left: none; }
  &:last-child {
    border-right: none; } }

.give-keyboard__button {
  display: block;
  width: 100%;
  height: 70px;
  @include f(t, 40);
  text-align: center;
  color: $color-text;
  @include trans();
  &:hover {
    background: $color-blue;
    color: #FFF; }
  &:active {
    background: lighten($color-blue, 5%); }
  &._ok-button {
    color: $color-green;
    @include f(b);
    &:hover {
      background: $color-green;
      color: #FFF; }
    &:active {
      background: lighten($color-green, 5%); } } }

.give-designate-button {
  position: relative;
  z-index: 1;
  left: 1px;
  display: block;
  width: 50%;
  width: calc(50% + 1px);
  margin: auto;
  border: 1px solid $color-text;
  border-top: none;
  border-radius: 0 0 3px 3px;
  @include f(b, 14);
  height: 45px;
  line-height: 44px;
  text-align: center;
  box-sizing: border-box;
  color: $color-text;
  text-transform: uppercase;
  transition: all 0.3s linear;
  cursor: pointer;
  .give-check__text {
    position: static;
    padding: 0;
    &:before {
      left: 12px;
      opacity: 0;
      transition: opacity 0.3s linear; }
    &:after {
      left: 18px;
      opacity: 0; } } }

.give-designate {
  position: relative;
  z-index: 0;
  padding: 0 24px 0;
  border-top: 1px solid $color-text;
  transition: all 0.3s linear;
  transition-property: background-color;
  &.is-active {
    background-color: $color-text;
    color: #FFF;
    margin-top: -1px;
    .give-designate-button {
      background-color: #FFF;
      .give-check__text {
        &:before,
        &:after {
          opacity: 1; }
        &:after {
          transition-delay: 0.2s; } } }
    .give-designate__inner {
      color: #FFF; } } }

.give-designate__inner {
  display: none;
  padding: 18px 28px 35px;
  color: #FFF; }

.give-payment-form {
  padding: 6px 15px 0px; }

.give-payment-form__footer {
  position: relative;
  margin-top: 30px;
  .give-secured-label {
    position: absolute;
    top: 50%;
    margin-top: -10px;
    left: 50%;
    margin-left: 174px; } }

.give-payment-tabs {
  box-sizing: border-box;
  padding: 15px 15px 0;
  margin: 0 -15px;
  position: relative;
  text-align: center;
  white-space: nowrap;
  font-size: 0;
  border-top: 1px solid $color-text; }

.give-payment-tabs-content {
  position: relative;
  margin: 22px 0 0;
  .slick-list {
    margin: 0 -15px; }
  .slick-slide {
    padding: 0 15px; } }

.give-payment-tabs-slide {
  display: none;
  @include f(t, 12);
  color: #9B9B9B;
  line-height: 1.1;
  @include clr;
  p {
    margin: 0; }
  .give-card-box {
    float: left;
    margin-right: 17px; }
  &:focus {
    outline: none; }
  &.is-active,
  &.slick-slide {
    display: block; } }

.give-payment-tabs__button {
  position: relative;
  display: inline-block;
  box-sizing: border-box;
  padding-bottom: 14px;
  vertical-align: top;
  margin-right: 26px;
  @include f(b, 14);
  line-height: 1.1;
  color: $color-text;
  opacity: 0.5;
  text-transform: uppercase;
  @include trans;
  &:before {
    opacity: 0;
    visibility: hidden;
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: $color-blue;
    @include trans; }
  &:last-child {
    margin-right: 0; }
  &:hover {
    opacity: 1; }
  &.is-active,
  &.slick-current {
    opacity: 1;
    &:before {
      opacity: 1;
      visibility: visible; } } }

.give-secured-label {
  display: inline-block;
  vertical-align: middle;
  @include f(b, 9);
  color: $color-text;
  line-height: 1;
  text-transform: uppercase;
  text-decoration: none;
  > * {
    display: inline-block;
    vertical-align: middle; }
  .give-icon {
    font-size: 20px;
    position: relative;
    margin-right: 2px; } }


.give-tooltip {
  position: absolute;
  z-index: 2;
  bottom: 100%;
  left: 0;
  margin-bottom: 17px;
  box-sizing: border-box;
  padding: 10px;
  border-radius: 3px;
  @include f(t, 15);
  line-height: 16px;
  color: #FFF;
  background: $color-text;
  &:after {
    content: "";
    position: absolute;
    z-index: -1;
    width: 14px;
    height: 14px;
    bottom: -7px;
    left: 11px;
    transform: rotate(45deg);
    background: $color-text; }
  &._red {
    background: $color-red;
    &:after {
      background: $color-red; } }
  &._blue {
    background: $color-blue;
    &:after {
      background: $color-blue; } }
  &._green {
    background: $color-green;
    &:after {
      background: $color-green; } } }

.give-dots {
  position: absolute;
  left: 0;
  top: 8px;
  right: 0;
  text-align: center;
  list-style-type: none;
  padding: 0;
  margin: 0;
  font-size: 0; }

.give-dots__dot {
  display: inline-block;
  vertical-align: top;
  width: 8px;
  height: 8px;
  box-sizing: border-box;
  border-radius: 50%;
  border: 1px solid #9B9B9B;
  @include trans;
  margin-right: 6px;
  &:last-child {
    margin-right: 0; }
  &.is-active {
    background: #9B9B9B; } }

%give-scrollble,
.give-scrollable {
  @include scrollbar {
    height: 5px;
    width: 5px; }
  @include scrollbar-track {
    height: 2px;
    width: 2px;
    background: transparent; }
  @include scrollbar-thumb {
    background: $color-border-grey; }
  @include scrollbar-track-piece {
    background: transparent; } }

.give-dropdown-box {
  position: relative;
  z-index: 10; }

.give-dropdown {
  position: absolute;
  z-index: 2;
  top: 100%;
  left: 0;
  background: #FFF;
  border: 1px solid $color-border-grey;
  border-radius: 3px;
  width: 100%;
  max-width: 300px;
  min-width: 160px;
  overflow: hidden;
  &._double {
    @include clr;
    &:before {
      content: "";
      position: absolute;
      top: 5px;
      bottom: 5px;
      left: 50%;
      border-left: 1px solid $color-border-grey; }
    .give-dropdown__list {
      width: 50%;
      float: left; } }
  &._date {
    max-width: 160px;
    height: 210px;
    text-align: center; } }

.give-dropdown__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  height: 100%;
  overflow: auto;
  max-height: 240px;
  @include scroll-touch; }

.give-dropdown__item {
  background: none;
  height: 30px;
  line-height: 30px;
  white-space: nowrap;
  width: 100%;
  padding: 0 10px;
  @include text-overflow;
  @include trans;
  box-sizing: border-box;
  transition-property: background-color, color;
  font-size: 16px;
  cursor: default;
  color: $color-text;
  &:hover,
  &.is-selected {
    background: $color-blue;
    color: #FFF; } }

.give-aside {
  padding: 41px 24px 24px;
  font-size: 16px;
  line-height: 1.25;
  box-sizing: border-box;
  max-width: $widget-max-width;
  margin: auto;
  > img {
    display: block;
    margin: auto;
    max-width: 100%; }
  > p {
    margin: 1em 0;
    &:first-child {
      margin-top: 0; }
    &:last-child {
      margin-bottom: 0; } } }

.give-video-container {
  margin: 35px auto;
  position: relative;
  &:first-child {
    margin-top: 0; }
  &:last-child {
    margin-bottom: 0; }
  &:before {
    content: "";
    display: block;
    width: 100%;
    padding-bottom: percentage(260/460); }
  > iframe {
    display: block;
    margin: auto; } }

.give-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }


.give-processing {
  display: inline-block;
  letter-spacing: 0.1em;
  > span {
    &:first-child {
      animation: give-dot-1 1.2s linear infinite; }
    &:nth-child(2) {
      animation: give-dot-2 1.2s linear infinite; }
    &:nth-child(3) {
      animation: give-dot-3 1.2s linear infinite; } } }

.give-processing-word {
  letter-spacing: -0.1em;
  perspective: 100px;
  > span {
    display: inline-block;
    animation: give-letter-anim 1.2s linear infinite;
    animation-fill-mode: both;
    transform-origin: center bottom;
    @for $i from 1 to 11 {
      &:nth-child(#{$i}) {
        animation-delay: ($i * 0.08)+s; } } } }


.give-confirmation-container {
  display: none; }


.give-recurring-gift {
  display: inline;
  position: relative;
  top: -2px; }

.give-recurring-gift__cell {
  display: inline-block;
  > * {
    vertical-align: middle; } }

.give-in-memory-of {
  display: inline-block;
  width: 210px;
  margin-left: 6px; }


@keyframes give-cursor {
  0%, 100% {
    opacity: 0; }
  40%, 40% {
    opacity: 1; } }

@keyframes give-letter-anim {
  0%, 60%, 100% {
    transform: translateY(0px);
    opacity: 1; }
  85% {
    transform: translateY(-3px);
    opacity: 0.5; } }

@keyframes give-dot-1 {
  0%, 100% {
    opacity: 0; }
  25%, 75% {
    opacity: 1; } }

@keyframes give-dot-2 {
  0%, 25%, 100% {
    opacity: 0; }
  50%, 75% {
    opacity: 1; } }

@keyframes give-dot-3 {
  0%, 50%, 100% {
    opacity: 0; }
  75% {
    opacity: 1; } }

@keyframes give-shake {
  0%, 100% {
    transform: translate3d(0, 0, 0); }
  10%, 30%, 50%, 70%, 90% {
    transform: translate3d(-5px, 0, 0); }
  20%, 40%, 60%, 80% {
    transform: translate3d(5px, 0, 0); } }
