.give-card-box {
  width: 100%;
  max-width: 320px;
  height: 202px;
  position: relative;
  box-sizing: border-box;
  perspective: 1000px;
  @include gpu;
  .give-card {
    position: absolute;
    top: 0;
    left: 0;
    transition: transform 0.3s linear;
    transform-style: preserve-3d;
    &:last-child {
      transform: rotateX(-90deg);
      visibility: hidden; } }
  &.is-active {
    .give-card-box__inner {
      transform: rotateX(-180deg); } }
  &.is-hide-front {
    .give-card {
      &:first-child {
        transform: rotateX(90deg); }
      &:last-child {
        visibility: visible; } } }
  &.is-show-back {
    .give-card {
      &:first-child {
        visibility: hidden; }
      &:last-child {
        visibility: visible;
        transform: rotateX(0deg); } } } }

