.give-card {
  position: relative;
  border-radius: 5px;
  width: 100%;
  max-width: 320px;
  height: 202px;
  box-sizing: border-box;
  color: #FFF;
  background: $color-text linear-gradient(-32deg, $color-text 0%, $color-text 50%, rgba(255,255,255,0.03) 50%, rgba(255,255,255,0.03) 100%); }

.give-card__front {
  box-sizing: border-box;
  padding: 73px 18px 12px 15px;
  position: relative;
  height: 100%;
  @include clr; }

.give-card__chip {
  width: 61px;
  height: 36px;
  position: absolute;
  top: 25px;
  left: 6px;
  background: img-url('svg/card-chip.svg') no-repeat center;
  background-size: contain; }

.give-card__label {
  display: block;
  @include f(t, 7, 350);
  text-transform: uppercase;
  color: #FFF;
  line-height: 1;
  margin-bottom: 5px;
  &._align-center {
    text-align: center; } }

.give-card__value {
  display: block;
  @include f(b, 13);
  line-height: 1;
  text-transform: uppercase;
  color: #FFF; }

.give-card__logo {
  position: absolute;
  top: 18px;
  right: 18px;
  height: 46px;
  max-width: 50%;
  @include vertical;
  white-space: nowrap;
  > img {
    display: inline-block;
    vertical-align: middle;
    max-width: 100%;
    max-height: 100%; } }

.give-card__number-field {
  margin-bottom: 11px; }

.give-card__holder-field {
  float: left;
  width: percentage(208/287); }

.give-card__date-field {
  float: right;
  width: percentage(72/287); }

.give-card__back {
  box-sizing: border-box;
  height: 100%;
  padding: 16px 15px 23px 9px; }

.give-card__dark-line {
  height: 34px;
  background: #272020;
  opacity: 0.8;
  color: #FFF;
  padding: 4px 11px;
  margin: 0 -15px 3px -9px;
  box-sizing: border-box; }

.give-card__white-line {
  height: 34px;
  margin: 0 -15px 2px -9px;
  background: #FFF;
  padding: 4px 9px;
  color: $color-text;
  box-sizing: border-box; }

.give-card__number {
  display: block;
  @include f(t, 13);
  line-height: 1; }

.give-card__cvv-field {
  text-align: right;
  margin-bottom: 8px;
  .give-card__label {
    display: inline-block;
    vertical-align: top;
    margin-right: 5px; }
  .give-field {
    display: inline-block;
    vertical-align: top;
    width: 77px; } }

.give-card__holder {
  float: left; }

.give-card__expires {
  float: right;
  text-align: right; }

.give-card__bottom-line {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 23px;
  text-align: center;
  background: rgba(#FFF, 0.13); }

.give-card__rotate-btn {
  vertical-align: top;
  height: 23px;
  width: 50px;
  text-align: center;
  color: #FFF;
  opacity: 0.5;
  @include trans();
  &:hover {
    opacity: 1; } }
