@function em($size, $context: 16) {
  @return ($size/$context) * 1em; }

@function img-url($image) {
  @return url('../img/#{$image}'); }

@mixin image($image) {
  background-image: img-url($image); }

@mixin trans($what: all, $dur: 0.15s, $easing: ease) {
  transition: $what $dur $easing; }

@mixin scroll-touch {
  -webkit-overflow-scrolling: touch; }

@mixin smoothfont {
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased; }

@mixin gpu {
  transform: translate3d(0,0,0); }

@mixin md {
  @include r(1023) {
    @content; } }

@mixin sm {
  @include r(767) {
    @content; } }

@mixin mobile {
  @include r(567) {
    @content; } }

@mixin shadow-edge {
  &:before,
  &:after {
    content: "";
    position: absolute;
    z-index: 1;
    top: 0;
    height: 100%;
    width: 60px;
    pointer-events: none; }
  &:before {
    left: -1px;
    background: linear-gradient(to right, rgba(white, 1) 0%, rgba(white, 0) 100%); }
  &:after {
    right: -1px;
    background: linear-gradient(to left, rgba(white, 1) 0%, rgba(white, 0) 100%); } }

