@font-face {
  font-family: "give-icons";
  src: url('../fonts/give-icons.eot');
  src: url('../fonts/give-icons.eot?#iefix') format('eot'),
    url('../fonts/give-icons.woff') format('woff'),
    url('../fonts/give-icons.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

$give-icon-arrow-left: "\e900";
$give-icon-at: "\e901";
$give-icon-bulb: "\e902";
$give-icon-chevron-down: "\e903";
$give-icon-chevron-left: "\e904";
$give-icon-chevron-right: "\e905";
$give-icon-close: "\e906";
$give-icon-eye-closed: "\e907";
$give-icon-eye-open: "\e908";
$give-icon-help: "\e909";
$give-icon-keyboard: "\e90a";
$give-icon-location: "\e90b";
$give-icon-minus: "\e90c";
$give-icon-person: "\e90d";
$give-icon-plus: "\e90e";
$give-icon-postal: "\e90f";
$give-icon-rotate: "\e910";
$give-icon-shield: "\e911";
$give-icon-street: "\e912";


%give-icon {
  font-family: "give-icons";
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.give-icon {
  @extend %give-icon;
  display: inline-block;
  position: relative;

  &._arrow-left:before { content: $give-icon-arrow-left; }
  &._at:before { content: $give-icon-at; }
  &._bulb:before { content: $give-icon-bulb; }
  &._chevron-down:before { content: $give-icon-chevron-down; }
  &._chevron-left:before { content: $give-icon-chevron-left; }
  &._chevron-right:before { content: $give-icon-chevron-right; }
  &._close:before { content: $give-icon-close; }
  &._eye-closed:before { content: $give-icon-eye-closed; }
  &._eye-open:before { content: $give-icon-eye-open; }
  &._help:before { content: $give-icon-help; }
  &._keyboard:before { content: $give-icon-keyboard; }
  &._location:before { content: $give-icon-location; }
  &._minus:before { content: $give-icon-minus; }
  &._person:before { content: $give-icon-person; }
  &._plus:before { content: $give-icon-plus; }
  &._postal:before { content: $give-icon-postal; }
  &._rotate:before { content: $give-icon-rotate; }
  &._shield:before { content: $give-icon-shield; }
  &._street:before { content: $give-icon-street; }

}
